  margin-bottom: 1vmin;
  text-align: center;
  border-top: 3px solid #95a5a6;
  border-bottom: 3px solid #95a5a6; }
  .experience:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    cursor: pointer; }

li {
  margin: .5vmin; }

ul {
  font-size: calc(10px + .5vmin);
  line-height: 1.5;
  width: 100%;
  height: 100%;
